@use "../../../scss/variables" as *;
.paginator_buttons {
  display: flex;
  margin-top: 1rem;
  button {
    min-width: 30px;
    border: 1px solid $ligthGrey;
    padding: 0.2rem 0.4rem;
    cursor: pointer;
    &:hover {
      background-color: $ligthGrey;
    }
  }
  .selectedPage {
    font-weight: bold;
  }
}
